import React from 'react'
import './style.scss'

let content;

class Masthead extends React.Component {

  constructor(props) {
    super(props);
    this.state = {content:'interface'};
    content = this.state.content;
  }

  change() {
    if (content == 'interface'){
    this.setState({content: 'empowerment'});
    content = this.state.content;
    } else if (content == 'empowerment'){
      this.setState({content: 'democracy'});
      content = this.state.content;
    } else {
      this.setState({content: 'interface'});
      content = this.state.content;
    }
  }


  render() {
    return(
      <div className="masthead">
        <div className="mast-ani mt-2">
          {/*{this.state.content}*/}
        </div>
        <div className="container-fluid">
          <div className="row">
            <div className="col-lg-3 col-md-12 offset-lg-1 mt-5">
              <p className="slogan-l">
              I design for <br/>
              _
              </p>
              <br/>
              <p className="text-white text-right d-none d-lg-block">
              <small>Dynamic and socially-responsive designer with a 3-year working experience specializing in UI design, UX research, front-end programming, participatory design, and data visualization</small>
              </p>
            </div>
            <div className="col-lg-7 col-md-12 ">
              <p className="slogan-r d-none d-lg-block d-md-none">
                <span className="change">____interface</span><br/>
                <span className="change mt-2">empowerment___</span><br/>
                <span className="change mt-2">___________democracy</span><br/>
              </p>
              <p className="slogan-r text-center d-lg-none d-md-block">
                <span className="change">interface</span><br/>
                <span className="change mt-2">empowerment</span><br/>
                <span className="change mt-2">democracy</span><br/>
              </p>
            </div>

          </div>
          <div className="row pt-2">
            <div className="col-lg-3 col-md-12 offset-lg-1 mt-5">

            </div>
          </div>
        </div>
      </div>
    )
  }

  componentDidMount() {
        this.interval = setInterval(() => this.change(), 1500);
  }

  componentWillUnmount() {
    clearInterval(this.interval);
  }

}



export default Masthead
